<template>
    <div>
        <div class="login_content" v-if="loadding">
            <canvas id="cas" class="htmleaf-container"></canvas>
            <el-form :model="ruleForm2" :rules="rules2" ref="ruleForm2" label-position="left" label-width="0px"
                     class="demo-ruleForm login-container" action="">
                <h3 class="title">系统登录</h3>
                <el-form-item prop="account">
                    <el-input type="text" v-model="ruleForm2.account" auto-complete="off" placeholder="账号"></el-input>
                </el-form-item>
                <el-form-item prop="checkPass">
                    <el-input type="password" v-model="ruleForm2.checkPass" auto-complete="off"
                              placeholder="密码"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-checkbox v-model="checked" checked class="remember">记住密码</el-checkbox>
                </el-form-item>
                <el-form-item style="width:100%;">
                    <el-button type="primary" style="width:100%;" @click.native.prevent="handleSubmit2"
                               :loading="logining">
                        登录
                    </el-button>
                    <!--<el-button @click.native.prevent="handleReset2">重置</el-button>-->
                </el-form-item>
            </el-form>
        </div>
        <loadding v-else/>
    </div>
</template>

<script>
    import {mapState} from 'vuex'
    import {mapGetters} from 'vuex'
    import loadding from './../components/loadding/loadding.vue'
    import AJAX from './../assets/js/ajax'
    export default {
        data() {
            return {
                loadding: true, //为true显示加载动画 为false显示页面
                logining: false,
                ruleForm2: {
                    account: 'admin',
                    checkPass: '123456'
                },
                rules2: {
                    account: [
                        {required: true, message: '请输入账号', trigger: 'blur'},
                        //{ validator: validaePass }
                    ],
                    checkPass: [
                        {required: true, message: '请输入密码', trigger: 'blur'},
                        //{ validator: validaePass2 }
                    ]
                },
                checked: true
            };
        },
        mounted: function () {
        },
        //解构赋值，获取state和getters里面的值
        /*computed: {...mapState(['num']),...mapGetters(['changeNum'])},*/
        components: {
            loadding
        },
        methods: {
            handleReset2() {
                this.$refs.ruleForm2.resetFields();
            },
            handleSubmit2(ev) {
                let _this = this;
                this.$refs.ruleForm2.validate((valid) => {
                    if (valid) {
                        this.logining = true;
                        let loginParams = {username: this.ruleForm2.account, password: this.ruleForm2.checkPass};
                        AJAX.post('sys/user/loginweb', loginParams, (res) => {
                            console.log(res.data);
                            _this.logining = false;
                            sessionStorage.setItem('Token', JSON.stringify(res.data[0].token));
                            _this.$router.push({path: '/home/Personal'});
                        })
                        /*_this.logining = false;
                        sessionStorage.setItem('Token', JSON.stringify('qwwwwwwwwwww'));
                        _this.$router.push({path: '/home'});*/
                        /*window.setTimeout(() => {
                         _this.logining = false;
                         sessionStorage.setItem('user', JSON.stringify(loginParams.username));
                         _this.$router.push({path: '/PersonalInfo'});
                         }, 1000)*/
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            }
        }
    }
</script>

<style scoped>
    #cas {
        display: block;
        height: 100%;
        left: 0;
        margin: 0;
        padding: 0;
        position: absolute;
        top: 0;
        width: 100%;
    }

    .login-container {
        background: rgba(255, 255, 250, 0.8) !important;
    }

    .login_content {
        width: 100%;
        height: 100vh;
        background: url('../assets/img/login/login_bg.jpg');
        position: relative;
        background-size: cover;
    }

    .login-container {
        position: relative;
        top: 180px;
        background: #fff;
        -webkit-border-radius: 5px;
        border-radius: 5px;
        -moz-border-radius: 5px;
        margin: 0 auto;
        width: 350px;
        padding: 35px 35px 15px 35px;
        /*border: 1px solid #eaeaea;*/
        /*box-shadow: 0 0 25px #cac6c6;*/
    }

    .title {
        margin: 0px auto 40px auto;
        text-align: center;
        font-size: 18px;
        color: #505458;
    }
</style>


